.profileScreen{
    height: 100vh;
    background-color: black;
    color: white;
    overflow-y: scroll;
}

.profileScreen__body{
    display: flex;
    flex-direction: column;
    width: 50%;
    margin-right: auto;
    margin-left: auto;
    padding-top: 8%;
    max-width: 800px;
}

.profileScreen__info{
    display: flex;
}

.profileScreen__info img{
    height: 100px;
}

.profileScreen__body h1{
    font-size: 60px;
    font-weight: 400;
    border-bottom: 1px solid #282c2d;
    margin-bottom: 20px;
}

.profileScreen__details{
    display: flex;
    flex-direction: column;
    color: white;
    margin-left: 25px;
    flex: 1;
}

.profileScreen__details p{
    color: white;
    margin-top: 5px;
}

.profileScreen__details h2{
    background-color: gray;
    padding: 15px;
    font-size: 15px;
    padding-left: 20px;
}

.profileScreen__signOut{
    padding: 10px 20px;
    font-size: 1rem;
    margin-top: 5px;
    width: 100%;
    color: white;
    background-color: #e50914;
    font-weight: 600;
    border: none;
    cursor: pointer;
}
